.Detail-projet
{
   /* display: flex;*/
    margin-top: 130px;
}

.Log-Title h1
{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 36px;
    line-height: 142.6%;
    color: #FF6060;

    margin-top: -0.10em;
}

.Div-lien {
    grid-gap: 1rem;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(auto-fit,minmax(100px,150px));
    margin-top: 0.9em;
}

.Div-lien a {
    text-decoration: none;
    color: #fff;
}

.Div-lien p {
    background: #ff6060;
    border-radius: 10px;
    color: #fff;
    font-family: Montserrat;
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    padding: 0.5em 1em;
    text-align: center;
}

.collapseApp {
    display: flex;
    gap: 2rem;
    margin-top: 20px;
    width: 100%;
}

/*Partie mobile*/
@media (max-width: 768px) {

    .collapseApp
    {
        flex-direction: column;
    }

    .Detail-projet
    {
        margin-top: 50px;
    }
}